<template>
  <div class="sizeAside-container" v-if="!loading">
    <div class="loading" v-if="status == 'loading'">loading...</div>
    <template v-else-if="status == 'login'">
      <div class="avatar">
        <Avatar
          url="https://img1.baidu.com/it/u=2681504758,1624692466&fm=26&fmt=auto&gp=0.jpg"
        />
      </div>
      <p style="font-size: 20px; margin: 10px 0; color: #fff">
        {{ data.siteTitle }}
      </p>
      <p
        style="color: rgb(101, 231, 170); cursor: pointer"
        @click="hanldeLoginOut"
      >
        退出
      </p>
    </template>
    <div class="login" v-else>
      <button @click="hanldeLeft">点我登陆</button>
    </div>
    <div class="menu"><Menu /></div>
    <div class="contact"><Contact /></div>
    <div class="form-container" ref="formContainer"></div>
  </div>
</template>

<script>
import Avatar from "@/components/Avatar/";
import Contact from "@/components/SizeAside/Contact/";
import Menu from "@/components/SizeAside/Menu/";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    Avatar,
    Contact,
    Menu,
  },
  computed: {
    ...mapState("setting", ["data", "loading"]),
    ...mapGetters("loginUser", ["status"]),
  },
  methods: {
    hanldeLeft() {
      this.$router.push("/login").catch(() => {});
    },
    hanldeLoginOut() {
      this.$store.dispatch("loginUser/loginOut");
      setTimeout(() => {
        this.$router.push("/login");
      }, 1000);
    },
  },
};
</script>

<style scoped lang="less">
.sizeAside-container {
  text-align: center;
  width: 300px;
  height: 100%;
  background: rgb(31, 34, 33);
  color: rgb(122, 107, 107);
}

.avatar {
  padding: 20px;
  display: flex;
  justify-content: center;
}

.menu,
.contact {
  margin-bottom: 40px;
}

.login {
  height: 100px;
  line-height: 100px;
  button {
    outline: none;
    padding: 10px 20px;
    border: none;
    color: #fff;
    border-radius: 10px;
    background: #8bc34a;
  }
}
</style>
