import { setting } from "@/api/setting"
import { titleControl } from "@/utils"
export default {
    namespaced: true,
    state: {
        loading: false,
        data: []
    },
    mutations: {
        setData(state, payload) {
            state.data = payload
        },
        setLoading(state, payload) {
            state.loading = payload
        }
    },
    actions: {
        async fetchData(ctx) {
            ctx.commit("setLoading", true)
            const resp = await setting();
            ctx.commit("setData", resp.data);
            ctx.commit("setLoading", false)
            if (resp.data.favicon) {
                let link = document.querySelector("link[rel='shortcut icon']")
                if (link) {
                    return;
                } else {
                    link = document.createElement("link");
                    link.rel = "shortcut icon"
                    link.href = resp.data.favicon
                    document.head.appendChild(link)
                }
            }
            if (resp && resp.data.siteTitle) {
                titleControl.setStoreTitle(resp.data.siteTitle)
            }
        }
    }
}