import getRootDom from "./getRootDom";
import Icon from "@/components/Icon"
import styles from "./showMessage.module.less"
/**
 * 
 * @param {String} content 提示内容
 * @param {String} type     提示类型
 * @param {HTMLElement} container  相对于那个html
 * @param {Number} duration     消失的时间
 */
export default function showMessage(options = {}) {
    const content = options.content || "";
    const type = options.type || "info";
    const container = options.container || document.body;
    const duration = options.duration || 1000;
    const div = document.createElement("div");
    const iconDom = getRootDom(Icon, { type })
    div.innerHTML = `<span class="${styles.icon}">${iconDom.outerHTML}</span>${content}`
    div.className = `${styles.message} ${styles[`message-${type}`]} `
    if (!container) {
        if (getComputedStyle(container).position == "static") {
            container.style.position = "relative"
        }
    }
    container.appendChild(div);
    div.clientHeight
    div.style.opacity = 1;
    div.style.transform = `translate(-50%, -50%)`
    setTimeout(() => {
        div.style.opacity = 0;
        div.style.transform = `translate(-50%, -50%) translateY(-40px)`;
        div.addEventListener("transitionend", function () {
            div.remove();
            options.callback && options.callback();
        }, { once: true })
    }, duration);
}