let resultTitle = "", sotreTitle = "";
function setTitle() {
    if (!resultTitle && !sotreTitle) {
        document.title = "loading..."
    } else if (resultTitle && !sotreTitle) {
        document.title = resultTitle
    } else if (sotreTitle && !resultTitle) {
        document.title = sotreTitle
    } else {
        document.title = `${resultTitle}-${sotreTitle}`
    }
}


export default {
    setTitle(title) {
        resultTitle = title;
        setTitle();
    },
    setStoreTitle(title) {
        sotreTitle = title;
        setTitle();
    }
}