import Vue from "vue"
import Vuex from "vuex"
import banner from "./banner"
import setting from "./setting"
import loginUser from "./loginUser"
import about from "./about"
Vue.use(Vuex)
const store = new Vuex.Store({
    modules: {
        banner,
        setting,
        loginUser,
        about
    }
});
window.store = store;
export default store;
