import eventBus from "@/eventBus.js"
import { shake } from "@/utils/"
import loadingUrl from "@/assets/loadingImage.gif"
let imgs = [];
function setImage(img) {
    img.dom.src = loadingUrl;
    const clientHeight = document.documentElement.clientHeight;
    const exact = img.dom.getBoundingClientRect();
    const height = exact.height || 150;
    if (exact.top >= -height && exact.top <= clientHeight) {
        const tempImage = new Image();
        tempImage.onload = function () {
            setTimeout(() => {
                img.dom.src = img.src
            }, 1000);

        }
        tempImage.src = img.src;
        imgs = imgs.filter(i => i !== img)

    }
}

function setImages(imgs) {
    for (const img of imgs) {
        setImage(img);
    }
}
function hanldeScroll() {
    setImages(imgs);
}
eventBus.$bus.$on("mainScroll", shake(hanldeScroll, 100))
export default {
    inserted(el, binding) {
        const img = {
            dom: el,
            src: binding.value
        }
        imgs.push(img)
        setImage(img)
    },
    unbind(el) {
        imgs = imgs.filter(img => img.dom !== el)
    }
}